.typography {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2;
    h1, h2, h3, h4, h5, h6, p, blockquote, dl, ol, ul {
        margin: 0;
        font-family: inherit;
        color: inherit;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &:first-child {
            margin-top: 0;
        }
    }

    h1, h2, h3, h4, h5, h6 {
        font-weight: 700;
        text-transform: uppercase;
    }

    h1 {
        font-size: 45px;
    }

    h2 {
        font-size: 35px;
    }

    h3 {
        font-size: 22px;
    }

    h4 {
        font-size: 18px;
    }

    a {
        display: inline-block;
        text-decoration: none;
        transition: all .3s;
        &::after {
            content: "";
            display: block;
            width: 100%;
            height: 1px;
            background-color: var(--mainColor);
            margin-top: -2px;
        }
        &:hover {
            color: var(--Grey);
            &::after {
                background-color: transparent;
            }
        }
    }

    mark {
        background-color: var(--White);
    }

    small {
        font-size: 80%;
        font-weight: 400;
    }

    b, strong {
        font-weight: 800;
    }

    blockquote {
        position: relative;
        margin: 30px 0;
        background-color: #f8f9fb;
        padding: 35px 25px 35px 53px;
        &::after {
            position: absolute;
            top: 35px;
            left: 25px;
            content: "";
            display: block;
            background-color: var(--itemBg);
            width: 4px;
            height: calc(100% - 70px);
        }
    }

    dl, ol, ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    li:not(:last-child) {
        margin-bottom: 17px;
    }

    ol ol, ol ul, ul ol, ul ul {
        margin-bottom: 0;
    }

    ul {
        list-style-position: inside;
        list-style: none;
        li {
            display: flex;
            align-items: flex-start;
            &::before {
                content: "-";
                margin-right: 11px;
            }
            span {
                display: block;
                flex-shrink: 1;
            }
        }
    }

    ol {
        counter-reset: counter;
        li {
            display: flex;
            counter-increment: counter;
            &::before {
                content: counter(counter) ". ";
                margin-right: 14px;
                font-weight: 700;
                font-size: 15px;
                line-height: 1.5;
            }
        }
    }

    table {
        border-collapse: collapse;
        table-layout: fixed;
        width: 100%;
        margin: 27px 0;
        font-size: 15px;
        font-weight: 600;
        &:last-child {
            margin-bottom: 0;
        }
        &:first-child {
            margin-top: 0;
        }
    }

    thead {
        background-color: var(--brandColor);
        th, td {
            text-align: left;
            color: var(--White);
        }
    }

    tr {
        border: 1px solid var(--borderColor);
    }

    th, td {
        padding: 23px 22px;
        box-sizing: border-box;
        vertical-align: top;
    }

    td:first-child, th:first-child {
        width: 466px;
    }
}
@media(max-width: 767px) {
    .typography {
        th, td {
            padding: 15px 10px;
        }
        th:first-child, td:first-child {
            width: auto;
        }
    }
}
@media(max-width: 767px) {
    .typography {
        font-size: 15px;
    }
}